.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

/* Ensure buttons are clickable and visible */
.App-header button {
    z-index: 10;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(900px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 3000px;
  margin-top: 20px;
}

.card {
  background-color: #3b414d;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
}

.title-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    margin-bottom: 20px;
}

.timestamp {
    font-size: 0.9rem;
    color: #aaa;
}

.card h3 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.tz-label {
    font-size: 0.8rem;
    color: #888;
    font-weight: normal;
}

.card.error {
  border: 1px solid #ff6b6b;
}

.stats {
  margin-top: 10px;
  text-align: right;
  font-size: 0.9rem;
  font-weight: bold;
}

/* Reduce font size in Recharts Brush component */
.recharts-brush-traveller text {
  font-size: 10px !important;
}

.recharts-brush text {
  font-size: 10px !important;
}
