.monitor-sidebar {
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    width: 350px;
    background-color: transparent; /* Changed from #222 to transparent to allow stacking */
    color: #eee;
    box-shadow: none; /* Shadow moved to content */
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    pointer-events: none; /* Let clicks pass through wrapper */
}

.monitor-sidebar.open {
    /* Lift the open sidebar above others */
    z-index: 1010;
}

.monitor-sidebar.exposed {
    transform: translateX(0);
}

.monitor-toggle {
    position: absolute;
    left: -50px;
    /* top set via inline style */
    width: 50px;
    height: 50px;
    background-color: #333;
    border-radius: 10px 0 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: -2px 2px 5px rgba(0,0,0,0.3);
    pointer-events: auto; /* Re-enable clicks */
    transition: background-color 0.2s;
}

.monitor-sidebar.open .monitor-toggle {
    background-color: #444; /* Highlight active tab */
    border-right: none;
    width: 51px; /* Overlap border */
    z-index: 1002;
}

.sidebar-content {
    padding: 20px;
    overflow-y: auto;
    flex: 1;
    background-color: #222; /* Moved background here */
    box-shadow: -2px 0 5px rgba(0,0,0,0.5); /* Moved shadow here */
    opacity: 0;
    pointer-events: none; /* DISABLE clicks by default */
    height: 100%;
}

.monitor-sidebar.open .sidebar-content {
    opacity: 1;
    z-index: 1001;
    pointer-events: auto; /* ENABLE clicks when open */
}
.indicator-light {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #555;
    border: 2px solid #333;
}

.indicator-light.active {
    background-color: #00ff00;
    box-shadow: 0 0 10px #00ff00;
}

.indicator-light.alert {
    background-color: #ff0000;
    box-shadow: 0 0 15px #ff0000;
    animation: blink 1s infinite;
}

@keyframes blink {
    0% { opacity: 1; }
    50% { opacity: 0.5; }
    100% { opacity: 1; }
}

/* Removed duplicate .sidebar-content block */

.control-group {
    margin-bottom: 20px;
}

.control-group label {
    display: block;
    margin-bottom: 5px;
    font-size: 0.9em;
    color: #aaa;
}

.control-group select, .control-group input {
    width: 100%;
    padding: 8px;
    background-color: #333;
    border: 1px solid #444;
    color: white;
    border-radius: 4px;
}

.btn-primary {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
}

.btn-primary:hover {
    background-color: #0056b3;
}

.btn-danger {
    background-color: #dc3545;
}

.btn-danger:hover {
    background-color: #a71d2a;
}

.logs-panel {
    background-color: #000;
    padding: 10px;
    height: 200px;
    overflow-y: auto;
    font-family: monospace;
    font-size: 0.8em;
    border: 1px solid #444;
    margin-top: 20px;
}

.log-entry {
    margin-bottom: 4px;
    border-bottom: 1px solid #222;
    padding-bottom: 2px;
}

.log-entry.alert {
    color: #ff6b6b;
    font-weight: bold;
}

.log-entry.warning {
    color: #ffd43b;
    font-weight: bold;
}

.log-entry.error {
    color: #fa5252;
}

.log-entry.poll {
    color: #888;
}

.log-time {
    color: #888;
    margin-right: 8px;
}
